
/*  */
const nickRegexp = /^([\u4E00-\uFA29]|[\uE7C7-\uE7F3]|[a-zA-Z]){2,20}$/;
/*  */
const nameRegexp = /^[\u4E00-\uFA29]|[\uE7C7-\uE7F3]$/;
//
const phoneReg = /^1\d{10}$/
/*  */
const checkCertificateNo = (certificateNo) => {
    if(certificateNo === null || certificateNo === "") return true
    if(certificateNo.length !== 18){
        return false
    }else{
        const address = certificateNo.substring(0,6);//6
        const birthday = certificateNo.substring(6,14);//8
        // const sequenceCode =  +certificateNo.substring(14,17);//3
        // const checkCode =  certificateNo.substring(17);//1
        //   console.log(":"+certificateNo+":"+address+":"+birthday+":"+sequenceCode+":"+checkCode);

          const province={11:"",12:"",13:"",14:"",15:"",21:"",22:"",23:" ",
          31:"",32:"",33:"",34:"",35:"",36:"",37:"",41:"",
          42:" ",43:"",44:"",45:"",46:"",50:"",51:"",52:"",
          53:"",54:" ",61:"",62:"",63:"",64:"",65:"",71:"",81:"",82:"",91:""}

          const year =  birthday.substring(0,4);   
          const month = birthday.substring(4,6);   
          const day = birthday.substring(6);
          const tempDate = new Date(+year,parseFloat(month)-1,parseFloat(day)); 
          if(province[parseInt((address.substr(0,2)))] === null || (tempDate.getFullYear()!==parseFloat(year) || tempDate.getMonth()!==parseFloat(month)-1 || tempDate.getDate()!==parseFloat(day))){//getFullYear()
                return false
          }else{
            const weightedFactors = [ 7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2, 1 ];//   
            const valideCode = [ 1, 0, 10, 9, 8, 7, 6, 5, 4, 3, 2 ];// 10X
                let certificateNoArray = certificateNo.split("");// 
                let sum = 0;// 
                if (certificateNoArray[17].toLowerCase() === 'x') {
                      certificateNoArray[17] = 10;// x10  
                }
               for ( var i = 0; i < 17; i++) {
                     sum += weightedFactors[i] * certificateNoArray[i];//    
              }
              let valCodePosition = sum % 11;// 
             if (certificateNoArray[17] === valideCode[valCodePosition]) {
                   return true
              } else {
                   return false
              }
         }
   }
}

const testRealName = (name) => {
    return name === null || name === "" ? true:nameRegexp.test(name)
}
const testPhone = (phone) => {
    return phone === null || phone === "" ? true:phoneReg.test(phone)
}
const testNickName = (name) => {
    return name === null || name === "" ? true:nickRegexp.test(name)
}

const IsPC =()=> {
    var userAgentInfo = navigator.userAgent.toLowerCase();
    //alert(userAgentInfo)
    var Agents = ["android", "iphone",
                "symbianos", "windows phone",
                "ipad", "ipod","rv:1.2.3.4","ucweb","ucbrowser"];
    var flag = true;
    for (var v = 0; v < Agents.length; v++) {
        if (userAgentInfo.indexOf(Agents[v]) > 0) {
            flag = false;
            break;
        }
    }
    return flag;
}
export {
    checkCertificateNo,
    testRealName,
    testPhone,
    testNickName,
    IsPC
}